		<!--遮罩层-->
		<div class="bj" v-show="myarea"></div>
		<!--选择地区-->
		<div class="myArea" v-show="myarea">
			<van-area :area-list="areaList" @confirm="onChange" @cancel="onClose" />
		</div>
	</div>
</template>

<script>
	export default {
		name: "xinzengdz",
		data: function() {
			return {
				areaList: {
					province_list: {
						'110000': '北京市',
						'120000': '天津市'
					},
					city_list: {
						'110100': '北京市',
						'110200': '县',
						'120100': '天津市',
						'120200': '县'
					},
					county_list: {
						'110101': '东城区',
						'110102': '西城区',
						'110105': '朝阳区',
						'110106': '丰台区',
						'120101': '和平区',
						'120102': '河东区',
						'120103': '河西区',
						'120104': '南开区',
						'120105': '河北区',
						// ....
					}
