			onChange(picker, value, index) {
				console.log(value, index)
				var areaName = ''
				for(var i = 0; i < picker.length; i++) {
					areaName = areaName + picker[i].name + ' '
				}
				this.xinzengData.diqu = areaName
				this.myarea = !this.myarea
			},
			// 关闭弹出层
			onClose(){
				this.myarea = !this.myarea
			},
